import React from "react";
import { Link } from "react-router-dom";

function Header() {
    return (
        <header style={headerStyle}>
            <h1>ToDoList</h1>
            <Link style={linkStyle} to="/" >Home</Link> | <Link style={linkStyle} to="/about" >About</Link>
        </header>
    )
}

const headerStyle = {
    background: "lightgreen",
    color: "white",
    textAlign: "center",
    paddinhg: "10px"
}

const linkStyle = {
    color: "blue",
    textDecoration: "none"
}

export default Header;